.leftInputCase	{
	float: left;
}

.rightInputCase	{
	float: right;

}

.modal-form label	{
	display: inline;
	text-align: left;
	float: left;
}

.modal-form input, .form-control	{
	background-color: #F1F9F7;
}

.modal-div	{
	display: block;
	width: 100%;
}

.leftInputCase	{
	margin-left: 10px;
}

.modal-form	{
	display: block;
	width: 70%;
	height: auto;
	border: groove 0.5em #67B19D;
	border-radius: 2em;
	background-color: #F1F9F7;
}

.form-control	{
	padding: 10px;
	margin: 10px;
	width: 400px;
	text-align: left;
	margin-left: 200px;
}

.btn-modal-close	{
	margin: auto;
}	
.modal-body {
	width: auto;
}

.modal-body .form-control	{
	color: teal;
	margin-left: 0px;
	width: 60%;
	float: right;
	text-align: center;
}

#modal_chkbx	{
	margin-right: 500px;
	margin-top: 15px;
	margin-bottom: 10px;
	width: 20px;
	height: 20px;
}
.modal-body label	{
	float: left;
	clear: left;
	padding-right: 5px;
	margin-left: 20px;
	padding-top: 25px;
	font-size: large;
}

